@import '../../variables';
@import '../../mixins';

.PayAndFeed {
  background: #F2F2F2;
}

.charityPharagraph {
  display: none;

  @include media-from-tablet {
    display: block;
    margin: 15px 40px;
    text-align: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #828282;
  }

  @include media-from-desktop-min {
    margin: 30px 50px 0;
  }
}

.blockDonation {
  position: relative;
  display: block;
  margin: 35px auto;

  .charityCard {
    width: 245px;
    height: 245px;
    margin: 0 auto 100px;
    border: 2px solid #628d90;
    border-radius: 50%;

    img {
      display: block;
      margin: 70px auto 0;
    }

    h5 {
      font-weight: 900;
      font-size: 18px;
      line-height: 23px;
      text-align: center;
      margin: 18px 50px;
    }
  }

  .donationArrow {
    width: 60px;
    position: absolute;
    margin: -60px 90px;
    display: block;
    transform: translateX(50px) rotate(90deg) translateY(50px);

    &:nth-child(6) {
      display: none;
    }

    @include media-from-tablet {
      transform: translateX(360px) rotate(90deg) translateY(180px);
    }

    @include media-from-desktop-min {
      width: 40px;
      transform: translateX(190px) rotate(0deg) translateY(180px);
    }

    @include media-from-desktop {
      width: 105px;
      transform: translateX(300px) rotate(0deg) translateY(180px);
    }
  }

  &:nth-child(3) {
    margin-bottom: -60px;
  }

  @include media-from-tablet {
    width: 100%;
  }

  @include media-from-desktop-min {
    display: flex;
    padding: 0 150px;
  }
}
