@import '../../variables';

.heading {
  padding: 0;
  margin: 0;
  text-align: center;
  font-family: $font;
  font-style: normal;
  font-size: 36px;
  line-height: 42px;
  font-weight: 900;
}

@media screen and (min-width: 640px) {
  .heading {
    font-size: 48px;
    line-height: 56px;
  }
}
